<template>
  <v-container fluid class="down-top-padding">
    <v-row justify="center">
      <v-col cols="12" lg="8" md="8" sm="8" xs="11">
        <v-card>
          <v-card-text>
            <h3
              class="title blue-grey--text text--darken-2 font-weight-regular"
            >
               
            </h3>
            <h6 class="font-weight-light">
                    
            </h6>
          </v-card-text>
          <v-divider></v-divider>
          <v-card-text>
            <div>
              <span> : </span>
              <span>{{ this.fetchedUser.id }}</span>
            </div>
            <div>
              <span>  : </span>
              <span>{{ this.fetchedUser.username }}</span>
            </div>
          </v-card-text>
          <v-card-text>
            <v-row justify="center">
                <v-col
                  cols="12"
                  md="8"
                  sm="10"
                >
                    <v-text-field
                    type="text"
                    v-model="user.name"
                    label=" "
                    :rules="[
                        rules.required,
                        rules.min4,
                    ]"
                    filled
                    background-color="transparent"
                    ></v-text-field>
                </v-col>
                <v-col
                  cols="12"
                  md="8"
                  sm="10"
                >
                    <v-text-field
                    type="text"
                    v-model="user.phone"
                    label=""
                    :rules="[
                        rules.min11,
                    ]"
                    filled
                    background-color="transparent"
                    ></v-text-field>
                </v-col>
                <v-col
                  cols="12"
                  md="8"
                  sm="10"
                >
                    <v-text-field
                    type="text"
                    v-model="user.email"
                    label=""
                    :rules="[
                        rules.emailMatchOnly,
                    ]"
                    filled
                    background-color="transparent"
                    ></v-text-field>
                </v-col>

            </v-row>



            <v-subheader> </v-subheader>

            <v-row justify="center">
              <v-col
                cols="12"
                md="8"
                sm="10"
              >
                <v-select
                  v-model="selectedPermissions"
                  :items="permissions"
                  item-text="persian_codename"
                  item-value="id"
                  label="  "
                  multiple
                  chips
                  hint="   "
                  persistent-hint
                ></v-select>
              </v-col>
            </v-row>

            <v-subheader>  </v-subheader>

            <v-row justify="center">
                <v-col
                  cols="12"
                  md="8"
                  sm="10"
                >
                  <v-expansion-panels
                    v-model="panel"
                    hover
                    popout
                  >
                    <v-expansion-panel>
                      <v-expansion-panel-header>
                          
                      </v-expansion-panel-header>
                      <v-expansion-panel-content>
                        <v-row justify="center">
                          <v-col
                            cols="12"
                            md="8"
                            sm="10"
                          >
                            <v-text-field
                              v-model="user.password"
                              filled
                              background-color="transparent"
                              :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                              :rules="[rules.required, rules.min]"
                              :type="show1 ? 'text' : 'password'"
                              name="password"
                              label="  "
                              hint="     ."
                              counter
                              @click:append="show1 = !show1"
                            ></v-text-field>
                            <v-text-field
                              v-model="user.new_password"
                              filled
                              background-color="transparent"
                              :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                              :rules="[rules.required, rules.min]"
                              :type="show1 ? 'text' : 'password'"
                              name="password"
                              label="  "
                              hint="     ."
                              counter
                              @click:append="show1 = !show1"
                            ></v-text-field>
                          </v-col>
                        </v-row>
                      </v-expansion-panel-content>
                    </v-expansion-panel>
                  </v-expansion-panels>
                </v-col>
            </v-row>

            <v-row>
                <v-spacer></v-spacer>
                <v-btn
                @click="editUser"
                class="text-capitalize mt-5 element-0"
                color="primary"
                >
                 
                </v-btn>
            </v-row>
          </v-card-text>
        </v-card>

        <AlertComponent
          v-if="alert_msg"
          :alertMsg="alert_msg"
          :alertType="alert_type"
        >
        </AlertComponent>

      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import authHeader from '../../middleware/authHeader';

export default {
  name: "UserAdd",

  data: () => ({
    permissions: [],
    user: "",
    fetchedUser: "",
    selectedPermissions: [],
    show1: false,
    panel: false,
    rules: {
      required: (value) => !!value || " !",
      min: (v) => !v || v.length >= 8 || "  !",
      min4: (v) => !v || v.length >= 4 || !isNaN(v) || "  !",
      min11: (v) => !v || v.length == 11 || isNaN(v) || "    !",
      emailMatch: (v) =>
        !v.includes("@") ||
        /\S+@\S+\.\S+/.test(v) ||
        "    !",
      emailMatchOnly: (v) =>
        !isNaN(v) ||
        /\S+@\S+\.\S+/.test(v) ||
        "    !",
    },
    unhint: "    ",
    unconfirm: false,
    alert_msg: "",
    updated_at: "",
    newusermsg: "",
  }),
  components: {
    AlertComponent: () => import("@/components/sdComponents/AlertComponent"),
  },
  methods: {
    async editUser() {
      this.alert_msg = "";
      this.alert_type = null;
      const userId = this.$route.params.userId;
      try {
        const result = await this.$axios.post("/graphql", {
          query: `mutation {
            userUpdate (input: {
                id: "${userId}"
                phone: "${this.user.phone ? this.user.phone: ''}"
                email: "${this.user.email ? this.user.email: ''}"
                name: "${this.user.name ? this.user.name: ''}"
                password: "${this.user.password ? this.user.password: ''}"
                new_password: "${this.user.new_password ? this.user.new_password: ''}"
                permissions: "${this.selectedPermissions}"
              }) {
                id
                username
              }
          }`},
          authHeader(),
        );
        if (result.data.data.userUpdate.id) {
          this.alert_type = "success";
          this.alert_msg = "     .";
        } else {
          this.alert_type = "error";
          this.alert_msg = "    .";
        }
      } catch (error) {
        console.error(error);
      }
    },

    fetchUser: async function () {
        const userId = this.$route.params.userId;
        try {
          const result = await this.$axios.post("/graphql", {
            query: `query {
              userFind (id: "${userId}") {
                id
                name
                phone
                username
                createdAt
                status_auth
                email
                permissions {
                    id
                    codename
                    persian_codename
                }
              }
            }`},
            authHeader()
          );
          if (result.data.data.errors) {
            this.alert_msg = result.data.data.errors[0].message;
          } else if (result.data.data.userFind) {
            this.fetchedUser = result.data.data.userFind;
            this.selectedPermissions = this.fetchedUser.permissions.map((pm) => {
              return pm.id;
            });
            this.user = this.fetchedUser;
          }
        } catch (error) {
          console.error(error);
        }
    },

    fetchPermissions: async function () {
        try {
          const result = await this.$axios.post("/graphql", {
            query: `query {
              permissionFindAll {
                id
                codename
                persian_codename
              }
            }`,
          });
          if (result.data.data.errors) {
            this.alert_msg = result.data.data.errors[0].message;
          } else if (result.data.data.permissionFindAll) {
            this.permissions = result.data.data.permissionFindAll
          }
        } catch (error) {
          console.error(error);
        }
    },
  },
  created () {
      this.fetchUser()
      this.fetchPermissions()
  },
};
</script>
